import { CSSProperties, FC, SyntheticEvent, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './DualPill.scss';

// Types
import { BadgeTypes, TestMetadata } from 'types';

export interface DualPillProps {
  /** Class name */
  className?: string;
  /** Style */
  style?: CSSProperties;
  /** Size: small (height: 24px), medium (height: 32px), large (height: 40px) */
  size?: 'smaller' | 'small' | 'medium' | 'large';
  /** Color: primary, secondary, base, error, success, warning*/
  leftColor?: BadgeTypes;
  /** Color: primary, secondary, base, error, success, warning*/
  rightColor?: BadgeTypes;
  /** Sets a light background with dark text  */
  light?: boolean;
  /** Role */
  DualPillRole?: string;
  /** Determine if dismissable */
  showClose?: boolean;
  /** Close button function */
  onClose?: (e: SyntheticEvent) => void;
  // Test IDs for sections of DualPill
  testMetadata?: TestMetadata;
  /** Width: large is 16ch with ellipsis*/
  width?: 'large' | 'auto';
  /** Current status  */
  title?: string;
  /** Split Pill Left Title */
  leftTag?: string;
  /** Split Pill Right Title */
  rightTag?: string;
}

const DualPill: FC<DualPillProps> = ({
  className,
  style,
  size,
  leftColor,
  rightColor,
  light,
  DualPillRole,
  showClose,
  testMetadata,
  width,
  title,
  leftTag,
  rightTag,
  ...otherProps
}) => {
  const DualPillClassNames = useMemo(
    () =>
      classNames(
        'sb-DualPill',
        {
          'sb-DualPill_smaller': size === 'smaller',
          'sb-DualPill_small': !size || size === 'small',
          'sb-DualPill_medium': size === 'medium',
          'sb-DualPill_large': size === 'large',
          'sb-DualPill_light': light,
          'sb-DualPill_closable': showClose,
          'sb-DualPill_width-auto': width === 'auto',
          'sb-DualPill_width-large': width === 'large',
        },
        className,
      ),
    [className, light, showClose, size, width],
  );

  const DualPillLeftClassNames = useMemo(
    () =>
      classNames('sb-DualPill_split_left', {
        'sb-DualPill_primary': !leftColor || leftColor === 'primary',
        'sb-DualPill_secondary': leftColor === 'secondary',
        'sb-DualPill_base': leftColor === 'base',
        'sb-DualPill_error': leftColor === 'error',
        'sb-DualPill_success': leftColor === 'success',
        'sb-DualPill_warning': leftColor === 'warning',
      }),
    [leftColor],
  );

  const DualPillRightClassNames = useMemo(
    () =>
      classNames('sb-DualPill_split_right', {
        'sb-DualPill_primary': rightColor === 'primary',
        'sb-DualPill_secondary': !rightColor || rightColor === 'secondary',
        'sb-DualPill_base': rightColor === 'base',
        'sb-DualPill_error': rightColor === 'error',
        'sb-DualPill_success': rightColor === 'success',
        'sb-DualPill_warning': rightColor === 'warning',
      }),
    [rightColor],
  );

  return (
    <>
      <div {...otherProps} className={DualPillClassNames} {...testMetadata}>
        <div
          {...otherProps}
          className={DualPillLeftClassNames}
          style={style}
          role={DualPillRole}
          title={title}
          {...testMetadata}
        >
          {leftTag}
        </div>
        <div
          {...otherProps}
          className={DualPillRightClassNames}
          style={style}
          role={DualPillRole}
          title={title}
          {...testMetadata}
        >
          {rightTag}
        </div>
      </div>
    </>
  );
};

export default DualPill;
